import React from "react";
import { VideoBlock } from "./style";

export default () => {
  return (
    <VideoBlock
      src="https://www.youtube-nocookie.com/embed/y8Yv4pnO7qc?rel=0&amp;controls=0&amp;showinfo=0"
      frameborder="0"
      allowfullscreen
    />
  );
};



